import { Stack, Typography } from '@mui/material'
import { PageApi } from 'API/CMS/page'
import DeleteButton from 'components/Button/Delete'
import DesignHTMLSharePoint from 'components/DesignHTMLSharePoint'
import EditInput from 'components/EditInput'
import SubOwnerSelect from 'components/SubOwnerSelect'
import { PERMISSION } from 'interfaces'
import { useEffect, useState } from 'react'
import { Link, useLocation, useNavigate, useParams } from 'react-router-dom'
import { useAppDispatch, useAppSelector } from 'redux/hooks'
import { getPages } from 'redux/slices/CMS/Page'
import { getThemes } from 'redux/slices/CMS/Theme'
import { UIAction } from 'redux/slices/UI'
import Version from './Version'

export default function Update() {
	const { id, webId } = useParams<{ id: string; webId: string }>()
	const nav = useNavigate()
	const location = useLocation()
	const tokenInfo = useAppSelector((s) => s.UI.tokenInfo)
	const dispatch = useAppDispatch()
	const [loading, setLoading] = useState(false)
	const status = useAppSelector((s) => s.CMS.Page.status)
	const pages = useAppSelector((s) => s.CMS.Page.list)
	const page = pages.find((f) => f.id === Number(id))
	const [subOwner, setSubOwner] = useState<number[]>(() => page?.subOwner || [])
	const themeStatus = useAppSelector((s) => s.CMS.Theme.status)
	const themes = useAppSelector((s) => s.CMS.Theme.list)

	useEffect(() => {
		if (themeStatus === 'init') dispatch(getThemes())
	}, [dispatch, themeStatus])
	useEffect(() => {
		if (status === 'init') dispatch(getPages())
		else if (id && !page) nav(-1)
	}, [dispatch, id, nav, status, page])

	if (!id || !page || !tokenInfo) return null
	const theme = themes.find((f) => f.id === page.themeId)
	if (!theme) return null
	return (
		<Stack gap={1}>
			<Stack direction="row" alignItems="center" justifyContent="space-between" gap={2} sx={{}}>
				<Typography
					color="primary.dark"
					variant="h5"
					sx={{
						fontWeight: 600,
					}}
				>
					ページ設定
				</Typography>
				<Link to={`/cms/${webId}/page/${id}/delete`} state={{ location }}>
					<DeleteButton text="削除" sx={{ width: 150, py: '2px', fontSize: 14 }} />
				</Link>
			</Stack>

			<Stack gap={1} my={2}>
				<EditInput
					label="ページURL"
					value={page.url}
					apiEndpoint={'/api/cms/page/' + id}
					fieldName="url"
					readOnly={tokenInfo.permissions[PERMISSION.CMS] < 2}
				/>
				<EditInput
					label="ページ名"
					value={page.title}
					apiEndpoint={'/api/cms/page/' + id}
					fieldName="title"
					readOnly={tokenInfo.permissions[PERMISSION.CMS] < 2}
				/>
				<EditInput
					label="ページ説明"
					value={page.description}
					multiline
					rows={5}
					apiEndpoint={'/api/cms/page/' + id}
					fieldName="description"
					readOnly={tokenInfo.permissions[PERMISSION.CMS] < 2}
				/>
				{[2, 3, 6, 7].includes(tokenInfo.permissions[PERMISSION.CMS]) && (
					<SubOwnerSelect
						value={subOwner}
						setValue={async (val) => {
							setSubOwner(val)
						}}
					/>
				)}
			</Stack>
			<Stack alignItems="stretch" gap={1}>
				<Stack
					flex={1}
					justifyContent="center"
					alignItems="center"
					position="relative"
					zIndex={99}
					bgcolor="#3ac2"
					p={0.5}
					sx={{
						border: '1px solid #ccc',
					}}
				>
					<DesignHTMLSharePoint
						theme={theme}
						bodyCode={page.body}
						style={page.style}
						script={page.script}
						formStyle={page.formStyle}
						formScript={page.formScript}
						handleSavePage={async (body, style, script, formStyle, formScript) => {
							try {
								setLoading(true)
								const { error, message } = await PageApi.update({
									id,
									body,
									subOwner,
									style,
									script,
									formScript,
									formStyle,
								})
								dispatch(
									UIAction.addGlobalMessage({
										message,
										type: error ? 'error' : 'success',
									})
								)
							} finally {
								setLoading(false)
							}
						}}
						loading={loading}
					/>
				</Stack>
			</Stack>
			<Version versions={pages.filter((f) => f.uuid === page.uuid)} />
		</Stack>
	)
}
