import ExpandMoreIcon from '@mui/icons-material/ExpandMore'
import { Accordion, AccordionDetails, AccordionSummary, Button, Chip, Stack, Typography } from '@mui/material'
import { DataGrid } from '@mui/x-data-grid'
import { PageApi } from 'API/CMS/page'
import { format } from 'date-fns'
import { IPage } from 'interfaces'
import { useParams } from 'react-router-dom'
import { useAppDispatch, useAppSelector } from 'redux/hooks'
import { UIAction } from 'redux/slices/UI'

interface Props {
	versions: IPage[]
}

export default function Version({ versions }: Props) {
	const { id, webId } = useParams<{ id: string; webId: string }>()
	const tokenInfo = useAppSelector((s) => s.UI.tokenInfo)
	const dispatch = useAppDispatch()
	const page = useAppSelector((s) => s.CMS.Page.list).find((f) => f.id === Number(id))

	if (!id || !page || !tokenInfo || !webId) return null
	return (
		<Stack gap={2}>
			<Accordion>
				<AccordionSummary
					expandIcon={<ExpandMoreIcon />}
					sx={{
						my: 0,
						minHeight: '0 !important',
						bgcolor: '#acf2',
						'.Mui-expanded': {
							my: '12px',
						},
					}}
				>
					<Stack direction="row" alignItems="center" gap={1}>
						<Typography fontWeight={600}>最新バージョン {versions.find((f) => f.active)?.ver}</Typography>
					</Stack>
				</AccordionSummary>
				<AccordionDetails>
					<DataGrid
						rows={versions}
						columns={[
							{ field: 'ver', headerName: 'Version', width: 100 },
							{
								field: 'updatedAt',
								headerName: '更新日',
								width: 180,
								renderCell: (v) => {
									return format(new Date(v.row.updatedAt), 'yyyy年MM月dd日 HH:mm')
								},
							},
							{
								field: 'createdAt',
								headerName: '作成日',
								width: 140,
								renderCell: (v) => {
									return format(new Date(v.row.createdAt), 'yyyy年MM月dd日')
								},
							},
							{
								field: 'active',
								headerName: '動作',
								renderCell: (v) => {
									return !v.row.active ? (
										<Button
											variant="outlined"
											sx={{
												p: 0,
												px: 1,
												m: 0,
												minWidth: 0,
											}}
											color="secondary"
											size="small"
											onClick={async () => {
												try {
													const confirm = window.confirm('保存してないコンテンツは失われます。変更してもよろしいですか？')
													if (!confirm) return
													const { error, message } = await PageApi.active(v.row.id)
													dispatch(
														UIAction.addGlobalMessage({
															message,
															type: error ? 'error' : 'success',
														})
													)
												} finally {
												}
											}}
										>
											有効にする
										</Button>
									) : (
										<Chip label="使用中" size="small" color="success" />
									)
								},
							},
						]}
						initialState={{
							pagination: {
								paginationModel: { page: 0, pageSize: 30 },
							},
						}}
						pageSizeOptions={[30, 60]}
						sx={{
							'& .MuiDataGrid-row': {},
							'& .MuiDataGrid-cell:focus': {
								outline: 'none',
							},
							'& .MuiDataGrid-cell:focus-within': {
								outline: 'none',
							},
							'& .MuiDataGrid-columnHeaders': {
								bgcolor: 'primary.dark',
								color: '#fff',
							},
							'& .MuiDataGrid-columnHeaders svg': {
								color: '#fff',
							},
						}}
						getRowClassName={(v) => {
							return ''
						}}
						density="compact"
						hideFooterSelectedRowCount
						editMode="cell"
						disableColumnMenu
						disableRowSelectionOnClick
					/>
				</AccordionDetails>
			</Accordion>
		</Stack>
	)
}
